    padding: 12px;
    background: var(--el-fill-color-light);
    border-radius: 8px;
    min-height: 60px;
}
.preview-content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
:deep(.description-item) {
    word-break: break-all;
    word-wrap: break-word;
}
:deep(.el-descriptions__label) {
    width: 120px !important;
